// 음력 계산을 위한 객체
function LunaDate(year, month, day, leapMonth, ganji) {
    this.year = year;
    this.month = month;
    this.day = day;
    this.leapMonth = leapMonth;
    this.ganji = ganji;
}

// 양력을 음력으로 계산
function lunarCalc(year, month, day, type, leapmonth) {

    var sakGanjiIndex;
    var yearGanjiIndex;
    var monthGanjiIndex;
    var dayGanjiIndex;
    var solYear;
    var solMonth;
    var solDay;
    var lunYear
    var lunMonth;
    var lunDay;

    // lunLeapMonth는 음력의 윤달인지 아닌지를 확인하기위한 변수
    // 1일 경우 윤달이고 0일 경우 음달
    var lunLeapMonth;
    var lunMonthDay;
    var lunIndex;

    var solMonthDay = [31, 0, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

    /* range check */
    if (year < 1940 || year > 2101) {
        alert('1940년부터 2101년까지만 지원합니다');
        return new LunaDate(0, 0, 0, 0, null);
    }

    /* 속도 개선을 위해 기준 일자를 여러개로 한다 */
    if (year >= 2080) {
        /* 기준일자 양력 2080년 1월 1일 (음력 2079년 12월 10일) */
        solYear = 2080;
        solMonth = 1;
        solDay = 1;
        lunYear = 2079;
        lunMonth = 12;
        lunDay = 10;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2080 년 2월 28일 */
        lunMonthDay = 30; /* 2079년 12월 */
        sakGanjiIndex = 45;
        yearGanjiIndex = 35;
        monthGanjiIndex = 12;
        dayGanjiIndex = 54;
    } else if (year >= 2060) {
        /* 기준일자 양력 2060년 1월 1일 (음력 2059년 11월 28일) */
        solYear = 2060;
        solMonth = 1;
        solDay = 1;
        lunYear = 2059;
        lunMonth = 11;
        lunDay = 28;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2060 년 2월 28일 */
        lunMonthDay = 30; /* 2059년 11월 */
        sakGanjiIndex = 42;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 9;
    } else if (year >= 2040) {
        /* 기준일자 양력 2040년 1월 1일 (음력 2039년 11월 17일) */
        solYear = 2040;
        solMonth = 1;
        solDay = 1;
        lunYear = 2039;
        lunMonth = 11;
        lunDay = 17;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2040 년 2월 28일 */
        lunMonthDay = 29; /* 2039년 11월 */
        sakGanjiIndex = 8;
        yearGanjiIndex = 55;
        monthGanjiIndex = 12;
        dayGanjiIndex = 24;
    } else if (year >= 2020) {
        /* 기준일자 양력 2020년 1월 1일 (음력 2019년 12월 7일) */
        solYear = 2020;
        solMonth = 1;
        solDay = 1;
        lunYear = 2019;
        lunMonth = 12;
        lunDay = 7;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2020 년 2월 28일 */
        lunMonthDay = 30; /* 2019년 12월 */
        sakGanjiIndex = 33;
        yearGanjiIndex = 35;
        monthGanjiIndex = 12;
        dayGanjiIndex = 39;
    } else if (year >= 2000) {
        /* 기준일자 양력 2000년 1월 1일 (음력 1999년 11월 25일) */
        solYear = 2000;
        solMonth = 1;
        solDay = 1;
        lunYear = 1999;
        lunMonth = 11;
        lunDay = 25;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 2000 년 2월 28일 */
        lunMonthDay = 30;   /* 1999년 11월 */
        sakGanjiIndex = 30;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 54;
    }
    else if (year >= 1970) {
        /* 기준일자 양력 1970년 1월 1일 (음력 1969년 11월 24일) */
        solYear = 1970;
        solMonth = 1;
        solDay = 1;
        lunYear = 1969;
        lunMonth = 11;
        lunDay = 24;
        lunLeapMonth = 0;
        solMonthDay[1] = 28; /* 1970 년 2월 28일 */
        lunMonthDay = 30;   /* 1969년 11월 */
        sakGanjiIndex = 54;
        yearGanjiIndex = 45;
        monthGanjiIndex = 12;
        dayGanjiIndex = 17;
    }
    else {
        /* 기준일자 양력 1940년 1월 1일 (음력 1939년 11월 22일) */
        solYear = 1940;
        solMonth = 1;
        solDay = 1;
        lunYear = 1939;
        lunMonth = 11;
        lunDay = 22;
        lunLeapMonth = 0;
        solMonthDay[1] = 29; /* 1940 년 2월 28일 */
        lunMonthDay = 29;   /* 1939년 11월 */
        sakGanjiIndex = 18;
        yearGanjiIndex = 15;
        monthGanjiIndex = 12;
        dayGanjiIndex = 39;
    }

    lunIndex = lunYear - LUNAR_LAST_YEAR;

    // type이 1일때는 입력받은 양력 값에 대한 음력값을 반환
    // 2일 때는 입력받은 음력 값에 대한 양력값을 반환
    // 반복문이 돌면서 양력 값들과 음력 값들을 1일 씩 증가시키고
    // 입력받은 날짜값과 양력 값이 일치할 때 음력값을 반환함
    while (true) {

        if (type == 1 && year == solYear && month == solMonth && day == solDay) {
            let ganji = {
                yearGanji: ganjiArray[yearGanjiIndex % 60],
                monthGanji: ganjiArray[monthGanjiIndex % 60],
                dayGanji: ganjiArray[dayGanjiIndex % 60],
                sakGanji: ganjiArray[sakGanjiIndex % 60]
            }
            return new LunaDate(lunYear, lunMonth, lunDay, lunLeapMonth, ganji);
        } else if (type == 2 &&
            year == lunYear &&
            month == lunMonth &&
            day == lunDay &&
            leapmonth == lunLeapMonth) {
            return new LunaDate(solYear, solMonth, solDay, 0, null);
        }

        // 양력의 마지막 날일 경우 년도를 증가시키고 나머지 초기화
        if (solMonth == 12 && solDay == 31) {
            solYear++;
            solMonth = 1;
            solDay = 1;
            // 윤년일 시 2월달의 총 일수를 1일 증가
            if (solYear % 400 == 0) {
                solMonthDay[1] = 29;
            } else if (solYear % 100 == 0) {
                solMonthDay[1] = 28;
            } else if (solYear % 4 == 0) {
                solMonthDay[1] = 29;
            } else {
                solMonthDay[1] = 28;
            }
        }
        // 현재 날짜가 달의 마지막 날짜를 가리키고 있을 시 달을 증가시키고 날짜 1로 초기화
        else if (solMonthDay[solMonth - 1] == solDay) {
            solMonth++;
            solDay = 1;
        }
        else {
            solDay++;
        }

        // 음력의 마지막 날인 경우 년도를 증가시키고 달과 일수를 초기화
        if (lunMonth == 12 && ((lunarMonthTable[lunIndex][lunMonth - 1] == 1 && lunDay == 29) || (lunarMonthTable[lunIndex][lunMonth - 1] == 2 && lunDay == 30))) {
            lunYear++;
            lunMonth = 1;
            lunDay = 1;
            yearGanjiIndex++;
            monthGanjiIndex++;
            dayGanjiIndex++;
            sakGanjiIndex = dayGanjiIndex;
            if (lunYear > 2101) {
                alert("입력하신 달은 없습니다.");
                break;
            }

            // 년도가 바꼈으니 index값 수정
            lunIndex = lunYear - LUNAR_LAST_YEAR;

            // 음력의 1월에는 1 or 2만 있으므로 1과 2만 비교하면됨
            if (lunarMonthTable[lunIndex][lunMonth - 1] == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2) {
                lunMonthDay = 30;
            }
        }
        // 현재날짜가 이번달의 마지막날짜와 일치할 경우
        else if (lunDay == lunMonthDay) {

            // 윤달인데 윤달계산을 안했을 경우 달의 숫자는 증가시키면 안됨
            if (lunarMonthTable[lunIndex][lunMonth - 1] >= 3 && lunLeapMonth == 0) {
                lunDay = 1;
                lunLeapMonth = 1;
                dayGanjiIndex++;
                sakGanjiIndex = dayGanjiIndex;
            }
            // 음달이거나 윤달을 계산 했을 겨우 달을 증가시키고 lunLeapMonth값 초기화
            else {
                lunMonth++;
                lunDay = 1;
                lunLeapMonth = 0;
                monthGanjiIndex++;
                dayGanjiIndex++;
                sakGanjiIndex = dayGanjiIndex;
            }

            // 음력의 달에 맞는 마지막날짜 초기화
            if (lunarMonthTable[lunIndex][lunMonth - 1] == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 2) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 3) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 && lunLeapMonth == 0) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 4 && lunLeapMonth == 1) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 && lunLeapMonth == 0) {
                lunMonthDay = 30;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 5 && lunLeapMonth == 1) {
                lunMonthDay = 29;
            }
            else if (lunarMonthTable[lunIndex][lunMonth - 1] == 6) {
                lunMonthDay = 30;
            }
        }
        else {
            lunDay++;
            dayGanjiIndex++;
        }
    }
    return new LunaDate(0, 0, 0, 0, null);
}

// 양력을 음력날짜로 변환
export function solarToLunar(solYear, solMonth, solDay) {

    // 날짜 형식이 안맞을 경우 공백 반환
    if (!solYear || solYear == 0 ||
        !solMonth || solMonth == 0 ||
        !solDay || solDay == 0) {
        return "";
    }

    // 양력의 달마다의 일수
    var solMonthDays = [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31];

    // 윤년일 시 2월에 1일 추가
    if (solYear % 400 == 0 || (solYear % 4 == 0 && solYear % 100 != 0)) solMonthDays[1] += 1;
    if (solMonth < 1 || solMonth > 12 ||
        solDay < 1 || solDay > solMonthDays[solMonth - 1]) {
        return "";
    }

    /* 양력/음력 변환 */
    var date = lunarCalc(solYear, solMonth, solDay, 1);
    return date; // "음력 " + date.year + "년 " + (date.leapMonth ? "(윤)" : "") + date.month + "월 " + date.day;
}